import 'isomorphic-fetch';
import { Component } from 'react';

export default class extends Component {

    static async getInitialProps({ query }) {
        const idAudio = query.id;
        console.log(query.id);
        
        const requestClip = await fetch (`https://api.audioboom.com/audio_clips/${ idAudio }.mp3`);
        
        const { body : { audio_clip } } = await requestClip.json();
        console.log(audio_clip.title);
        return { audio_clip };
    }
    render(){

        const { audio_clip } = this.props
        return (
                <div>
                    <div className="cover">
                        <img src={ audio_clip.urls.post_image.original } alt="cover"/>
                    </div>
                    <div className="audioContainer">
                        <h2>{ audio_clip.title }</h2>
                        <h1>{ audio_clip.channel.title }</h1>
                        <audio src={ audio_clip.urls.high_mp3 } controls/>
                    </div>
                    <style jsx>{`
                        .cover {
                            background:  #55E6C1;
                            // width: 300px;
                            // height: 300px;
                            text-align: center;
                            line-height: 300px;
                        }
                        .cover img {
                            border-radius: 3px;
                            box-shadow: 0px 2px 6px rgba(0,0,0,0.15);
                            width: 30%;
                            height: 30%;
                            vertical-align: middle;
                        }
                        .audioContainer {
                            background: #218c74;
                            flex: 1;
                        }
                        h1 {
                            text-align: center;
                            font-weight: 100;
                            font-family: system-ui;
                            // background-color: white;
                            margin: 3 auto;
                            font-color: #f7f1e3;
                        }
                        h2 {
                            padding: 5px;
                            font-size: 25px;
                            font-weight: 600;
                            margin: 0;
                            text-align: center;
                            font-family: system-ui;
                        }
                        .audioContainer audio {
                            margin: 0 auto;
                            display: table;
                        }
                    `}</style>
                    <style jsx global>{`
                        body {
                            margin: 0;
                            background: white;
                        }
                    `}</style>
                </div>
        )
    }
}



